
.App {
  text-align: center;
  padding-top: .5rem;
 
}


.div_rem {
  width: 5rem;
  height: 5rem;
  background-color: black;
 margin: .25rem auto .25rem auto;

 
}

.div_px {
  width: 80px;
  height: 80px;
  background-color: black;
  margin: 4px auto 4px auto;
 
}
.div_em {
  width: 5em;
  height: 5em;
  background-color: black;
  margin: .25em auto .25em auto;
 
}

.para_rem{
  font-size: 1.5rem;
}

.para_px{
  font-size: 24px;
}

.outerem{
  font-size: .5em;
}
.para_em{
  font-size: 3em;
}



@media only screen and (max-width: 768px) {

  .para_rem{
    font-size: .75rem;
  }
  
  .para_px{
    font-size: 12px;
  }
  
  .outerem{
    font-size: .25em;
  }
  .para_em{
    font-size: 3em;
  }

}